.comicsPage {
    position: relative;
    text-align: center;
    margin: 0 auto;

    &-banner {
        margin-top: 50px;
        width: 1100px;
        height: 100px;
        background: rgb(35, 34, 34);
        display: flex;
        align-items: center;
    }

    &-banner__img {
        margin-left: 45px;
    }

    &-banner__title {
        color: rgb(255, 255, 255);
        font-family: Roboto Condensed;
        font-size: 24px;
        font-weight: 700;
        line-height: 28px;
        letter-spacing: 0%;
        text-align: left;
        width: 240px;
        margin-left: 85px;
    }

    &-banner__logo {
        margin-left: auto;
        margin-right: 25px;
    }
    
    &-banner__button {
        background-color: #9f0013; /* Красный цвет фона */
        color: #fff; /* Белый цвет текста */
        width: 170px; /* Ширина кнопки */
        height: 40px; /* Высота кнопки */
        font-family: 'Roboto Condensed', sans-serif;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center;
        line-height: 40px; /* Центрирование текста по вертикали */
        border: none;
        cursor: pointer;
        position: relative; /* Необходимо для позиционирования псевдоэлемента */
        display: inline-block;
        overflow: hidden; /* Для скрытия угла псевдоэлемента */
        transition: background-color 0.3s ease; /* Плавный переход фона */
        margin: 30px auto;
    
        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            right: -10px;
            border-width: 0 0 20px 20px; /* Вырезанный угол */
            border-style: solid;
            border-color: transparent transparent #fff transparent; /* Цвет нижнего угла (белый) */
            transition: border-color 0.3s ease; /* Плавное изменение цвета */
        }
    
        &::before {
            border-color: transparent transparent #f0f0f0 transparent; /* Изменение цвета угла при наведении */
        }
}

.charList{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &__item {
        width: 225px;
        height: 415px;
        margin-top: 50px;
    }
    
    &__img {
        width: 225px;
        height: 346px;
    }
    
    &__name {
        color: rgb(0, 0, 0);
        font-family: Roboto Condensed;
        font-size: 14px;
        font-weight: 700;
        line-height: 16px;
        letter-spacing: 0%;
        text-align: left;
    }
    
    &__price {
        color: rgba(0, 0, 0, 0.6);
        font-family: Roboto Condensed;
        font-size: 14px;
        font-weight: 700;
        line-height: 16px;
        letter-spacing: 0%;
        text-align: left;
        padding-top: 10px;
    }

    }
}